.feedback {
	background-color: #f4f4f4;
	padding-bottom: 71px;
	&-header {
		text-align: center;
		font-size: 27px;
		font-weight: 700;
		line-height: 18px;
		text-transform: uppercase;
		padding-top: 57px; }
	.slick-next {
